*,
*:after,
*:before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.clearfix:before,
.clearfix:after {
    content: '';
    display: table;
}

.clearfix:after {
    clear: both;
}

:root {

    --theme-coffee: #391400;
    --theme-red: #EF6D58;
    --theme-dark: #28293E;
    --theme-bg-light: #FDF0E9;
    --white: #fff;
    --dark: #000;
}
body.offscroll{
    overflow-y: hidden;
}



@font-face {
    font-family: "Montserrat-Bold";
    src: url(./MyComponents/fonts/Montserrat-Bold.ttf);
}

@font-face {
    font-family: "Montserrat-Medium";
    src: url(./MyComponents/fonts/Montserrat-Medium.ttf);
}

@font-face {
    font-family: "Montserrat-Regular";
    src: url(./MyComponents/fonts/Montserrat-Regular.ttf);
}

a {
    text-decoration: none;
}

button,
a,
p {
    font-family: "Montserrat-Medium";

}

.head_mini {
    font-family: "Montserrat-Regular";

}
.logo,
.page_title_banner,
.banner_txt {
    font-family: "Montserrat-Bold";

}

.header_scroll.navbar {
    backdrop-filter: blur(10px);
}

@media (max-width: 991.98px) {
    .offcanvas-collapse {
        position: fixed;
        top: 56px;
        /* Height of navbar */
        bottom: 0;
        right: 100%;height: 100vh;overflow-y: scroll;
        width: 100%;
        padding-right: 1rem;
        padding-left: 1rem;
        overflow-y: auto;
        visibility: hidden;
        background-color: #28293eed;
        transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }

    .offcanvas-collapse.open {
        visibility: visible;
        transform: translateX(100%);
    }
}

.head_mini {
    color: var(--theme-red);
}.banner_section {
    height: auto;
     padding-top: 5rem !important;
    background-color: var(--theme-dark);
}
@media (min-width:992px) {
    .banner_section {
        height: calc(100vh);
        background-color: var(--theme-dark);
    }
}


.cust_btn {
    background: var(--theme-red);
    padding: 10px 10px;
    color: var(--theme-dark);
    border-radius: 5px;
    border: 1px solid transparent;
    transition: .3s;

}

.cust_btn:hover {
    background: transparent;
    color: var(--white);
    border: 1px solid var(--theme-red);


}
button:focus{
    all: unset;
}
.navbar-nav  li a{
    color: var(--white);
}
.half_banner{
    height: 60vh;
    background-color: var(--theme-dark);
}

.hamburger .line{
    width: 25px;
    height: 2px;
    background-color: #ecf0f1;
    display: block;
    margin: 6px auto;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  
  .hamburger:hover{
    cursor: pointer;
  }
  /* ONE */
  
  #hamburger-1.is-active .line:nth-child(2){
    opacity: 0;
  }
  
  #hamburger-1.is-active .line:nth-child(1){
    -webkit-transform: translateY(4px) rotate(45deg);
    -ms-transform: translateY(4px) rotate(45deg);
    -o-transform: translateY(4px) rotate(45deg);
    transform: translateY(4px) rotate(45deg);
  }
  
  #hamburger-1.is-active .line:nth-child(3){
    -webkit-transform: translateY(-12px) rotate(-45deg);
    -ms-transform: translateY(-12px) rotate(-45deg);
    -o-transform: translateY(-12px) rotate(-45deg);
    transform: translateY(-12px) rotate(-45deg);
  }
  .navbar-toggler:focus {
    text-decoration: none;
    outline: 0;
    box-shadow:none;
}